import KcAppBase, { defaultKcProps } from 'keycloakify'
import { lazy, Suspense, useEffect, useState } from 'react'
import './global/KcApp.css'
import { useI18n } from './i18n'
import type { KcContext } from './kcContext'

const Register = lazy(() => import('./screens/register'))
const Login = lazy(() => import('./screens/login'))
const LoginResetPassword = lazy(() => import('./screens/loginResetPassword'))
const LoginUpdatePassword = lazy(() => import('./screens/loginUpdatePassword'))
const Error = lazy(() => import('./screens/error'))
const IdpReviewUserProfile = lazy(() => import('./screens/idpReviewUserProfile'))
const Info = lazy(() => import('./screens/info'))
const LoginConfigTotp = lazy(() => import('./screens/loginConfigTotp'))
const LoginIdpLinkConfirm = lazy(() => import('./screens/loginIdpLinkConfirm'))
const LoginIdpLinkEmail = lazy(() => import('./screens/loginIdpLinkEmail'))
const LoginOtp = lazy(() => import('./screens/loginOtp'))
const LoginPageExpired = lazy(() => import('./screens/loginPageExpired'))
const LoginPassword = lazy(() => import('./screens/loginPassword'))
const LoginUpdateProfile = lazy(() => import('./screens/loginUpdateProfile'))
const LoginUsername = lazy(() => import('./screens/loginUsername'))
const LoginVerifyEmail = lazy(() => import('./screens/loginVerifyEmail'))
const LogoutConfirm = lazy(() => import('./screens/logoutConfirm'))
const RegisterUserProfile = lazy(() => import('./screens/registerUserProfile'))
const UpdateUserProfile = lazy(() => import('./screens/updateUserProfile'))

export type Props = {
  kcContext: KcContext
}

export default function KcApp({ kcContext }: Props) {
  const [isAppLoaded, setIsAppLoaded] = useState(false)

  useEffect(() => {
    setTimeout(() => {
      setIsAppLoaded(true)
    }, 500)
  }, [])

  useEffect(() => {
    if (!isAppLoaded) return
    const kcHeaderWrapper = document?.getElementById('kc-header-wrapper')
    const firstChild = kcHeaderWrapper?.firstChild

    if (kcHeaderWrapper && firstChild?.nodeName !== 'A') {
      kcHeaderWrapper?.addEventListener('click', () =>
        window.open('https://www.db1.com.br/', '_self')
      )
      kcHeaderWrapper.style.cursor = 'pointer'
    }
  }, [isAppLoaded])

  const i18n = useI18n({ kcContext })

  //NOTE: Locales not yet downloaded
  if (i18n === null) {
    return null
  }

  const props = {
    i18n,
    ...defaultKcProps,
    // NOTE: The classes are defined in ./KcApp.css
    kcHtmlClass: 'kc-html',
    kcHeaderWrapperClass: 'my-color',
    kcLoginClass: 'login-class',
    kcInputClass: 'input-class',
    kcLabelClass: 'label-class',
    kcButtonClass: 'button-class',
  }

  return (
    <Suspense>
      {(() => {
        switch (kcContext.pageId) {
          case 'login.ftl':
            return <Login {...{ kcContext, ...props }} />
          case 'register.ftl':
            return <Register {...{ kcContext, ...props }} />
          case 'login-reset-password.ftl':
            return <LoginResetPassword {...{ kcContext, ...props }} />
          case 'login-update-password.ftl':
            return <LoginUpdatePassword {...{ kcContext, ...props }} />
          case 'error.ftl':
            return <Error {...{ kcContext, ...props }} />
          case 'idp-review-user-profile.ftl':
            return <IdpReviewUserProfile {...{ kcContext, ...props }} />
          case 'info.ftl':
            return <Info {...{ kcContext, ...props }} />
          case 'login-config-totp.ftl':
            return <LoginConfigTotp {...{ kcContext, ...props }} />
          case 'login-idp-link-confirm.ftl':
            return <LoginIdpLinkConfirm {...{ kcContext, ...props }} />
          case 'login-idp-link-email.ftl':
            return <LoginIdpLinkEmail {...{ kcContext, ...props }} />
          case 'login-otp.ftl':
            return <LoginOtp {...{ kcContext, ...props }} />
          case 'login-page-expired.ftl':
            return <LoginPageExpired {...{ kcContext, ...props }} />
          case 'login-password.ftl':
            return <LoginPassword {...{ kcContext, ...props }} />
          case 'login-update-profile.ftl':
            return <LoginUpdateProfile {...{ kcContext, ...props }} />
          case 'login-username.ftl':
            return <LoginUsername {...{ kcContext, ...props }} />
          case 'login-verify-email.ftl':
            return <LoginVerifyEmail {...{ kcContext, ...props }} />
          case 'logout-confirm.ftl':
            return <LogoutConfirm {...{ kcContext, ...props }} />
          case 'register-user-profile.ftl':
            return <RegisterUserProfile {...{ kcContext, ...props }} />
          case 'update-user-profile.ftl':
            return <UpdateUserProfile {...{ kcContext, ...props }} />
          default:
            return <KcAppBase {...{ kcContext, ...props }} />
        }
      })()}
    </Suspense>
  )
}
